import { Factory, ModelManager, ModelFile, Serializer, } from "@accordproject/concerto-core";
export function validateInstance(callback, jSonModels, instanceJson) {
    var models = JSON.parse(jSonModels);
    var modelManager = new ModelManager({ strict: true });
    var modelFiles = models.map(function (item) { return new ModelFile(modelManager, item); });
    modelFiles.sort(function (a, b) { return a.getNamespace().localeCompare(b.getNamespace()); });
    modelManager.addModelFiles(modelFiles);
    try {
        var factory = new Factory(modelManager);
        var serializer = new Serializer(factory, modelManager);
        var instance = JSON.parse(instanceJson);
        var resource = serializer.fromJSON(instance);
        callback(null, {
            instance: JSON.stringify(serializer.toJSON(resource)),
            id: resource.isIdentifiable() ? resource.getIdentifier() : undefined,
            isValid: true,
        });
    }
    catch (error) {
        callback(null, {
            isValid: false,
            errorMessage: error.message,
        });
        return;
    }
}
